<script>
import { useRouter } from "vue-router"
// import { ExtCall } from "@/utils/extCall"
import { useGlobalStore } from "@/store/global"

export default {
  props: {
    left: String,
    path: String,
    clickFun: Function,
  },
  setup(props) {
    const globalStore = useGlobalStore()
    const globalGoto = globalStore.goto

    const router = useRouter()
    let iconCode = ""
    let text = ""
    const state = props.left ? props.left : ""
    if (state == "chevronArrow") {
      iconCode = "fa fa-chevron-left"
    } else {
      iconCode = "far fa-arrow-left"
      text = "上一步"
    }
    const btnClick = () => {
      if (props.path == "-1") {
        try {
          globalGoto("back")
        } catch (error) {
          console.log(error)
          router.back()
        }
      } else if (props.path) {
        router.push({ path: props.path })
      } else {
        if (props.clickFun) {
          props.clickFun()
        } else {
          router.push({ path: "/home" })
        }
      }
    }

    return { iconCode, text, btnClick }
  },
}
</script>

<template>
  <header class="c-header c-header-3">
    <nav class="navbar ui-navbar">
      <ul class="navbar-nav">
        <li class="nav-item" @click="btnClick">
          <a class="nav-link"
            ><i :class="iconCode" ></i>{{ text }}</a
          >
        </li>
      </ul>
      <h1 class="navbar-brand"></h1>
      <ul class="navbar-nav">
        <li class="nav-item">
          <a href="javascript:void(0);" class="nav-link"></a>
        </li>
      </ul>
    </nav>
  </header>
</template>

