<script>
import { ref, reactive, onMounted, watch } from 'vue'
import { useRouter } from "vue-router"
import { Toast } from '../../components/global/swal'
import Header from "../../components/global/header/HeaderArea2.vue"
import { apiPostServiceInfo, apiGetJobType, apiGetExperience, apiGetLanguage, apiGetServiceRequirement } from "@/api/api"
import { useLanguageStore, useJobStore, useExperienceStore } from '@/store/global'
import { focusInput } from '@/utils/fixFooter'
// import { ExtCall } from "@/utils/extCall"

export default {
  setup() {

    const languageStore = useLanguageStore()
    const jobStore = useJobStore()
    const experienceStore = useExperienceStore()
    const router = useRouter()
    const serviceData = reactive({})
    // 控制頁面
    let stepStyle = ref(["nav-link active", "nav-link", "nav-link"])
    let showPage = ref("page1")
    let title = ref("服務資料")
    const backToPage1 = () => {
      showPage.value = 'page1'
      stepStyle.value = ["nav-link active ", "nav-link", "nav-link"]
      title.value = "基本資料"
    }

    //page1
    serviceData.serviceRequirementId = "1"
    serviceData.gender = "None"
    serviceData.seniorityId = 'exp1'
    serviceData.languageId = []
    serviceData.isHasLicense = '0'

    const save = () => {
      if (serviceData.languageId.length == 0) {
        Toast("請至少填寫一項語言能力")
      } else {
        showPage.value = 'page2'
        stepStyle.value = ["nav-link visited", "nav-link active", "nav-link"]
        title.value = "照顧需求"
      }
    }

    //page2
    serviceData.serviceId = []
    let postData = ref({})
    const save2 = async () => {
      if (serviceData.serviceId.length == 0) {
        Toast("請至少填寫一項服務需求")
      } else {
        postData.value.serviceRequirementId = parseInt(serviceData.serviceRequirementId)
        postData.value.gender = serviceData.gender
        postData.value.seniorityId = parseInt(serviceData.seniorityId.replace(/[^0-9]/gi, ""))
        postData.value.languageId = serviceData.languageId.map((x) => parseInt(x.replace(/[^0-9]/gi, "")))
        postData.value.isHasLicense = parseInt(serviceData.isHasLicense)
        postData.value.serviceId = serviceData.serviceId
        // console.log(postData.value)
        let msg = await apiPostServiceInfo(postData.value)
        if (msg.data == "新增成功") {
          Toast(msg.data)
          router.push({ path: '/requirement/list' })
        } else {
          Toast(msg.message)
        }
      }
    }


    //取服務類別、年資、語言、服務需求清單
    let jobTypeList = ref([])
    let expList = ref([])
    let langList = ref([])
    let serviceRequirementList = ref([])
    onMounted(async () => {
      focusInput()
      //取工作類別
      if (jobStore.jobTypeList == undefined) {
        // call api
        jobTypeList.value = await apiGetJobType()
        jobTypeList.value = jobTypeList.value.data.data
        jobStore.jobTypeList = jobTypeList.value
      } else {
        jobTypeList.value = jobStore.jobTypeList
      }
      //取服務年資
      if (experienceStore.experienceTypeList == undefined) {
        // call api
        expList.value = await apiGetExperience()
        expList.value = expList.value.data.data
        experienceStore.experienceTypeList = expList.value
      } else {
        expList.value = experienceStore.experienceTypeList
      }
      //取語言
      if (languageStore.languageTypeList == undefined) {
        langList.value = await apiGetLanguage()
        langList.value = langList.value.data.data
        languageStore.languageTypeList = langList.value
      } else {
        langList.value = languageStore.languageTypeList
      }
      serviceRequirementList.value = await apiGetServiceRequirement({ id: serviceData.serviceRequirementId })
      serviceRequirementList.value = serviceRequirementList.value.data.data
      // console.log(serviceRequirementList.value)
    })
    watch(() => serviceData.serviceRequirementId, async () => {
      serviceRequirementList.value = await apiGetServiceRequirement({ id: serviceData.serviceRequirementId })
      serviceRequirementList.value = serviceRequirementList.value.data.data
      serviceData.serviceId = []
      // console.log(serviceRequirementList.value)
    })

    return { serviceData, jobTypeList, expList, langList, serviceRequirementList, stepStyle, showPage, title, save, backToPage1, save2 }
  },
  components: { Header },
}
</script>

<template>
  <Header v-if="showPage == 'page1'" path="/requirement/list" />
  <Header v-if="showPage == 'page2'" :clickFun="backToPage1" />
  <div class="requirementAdd">
    <div class="mb-3 step-container">
      <ul class="nav justify-content-center">
        <!-- 已完成 visited，目前 active -->
        <li class="nav-item">
          <a href="javascript:void(0);" :class=stepStyle[0]><span>1</span></a>
        </li>
        <li class="nav-item">
          <a href="javascript:void(0);" :class=stepStyle[1]><span>2</span></a>
        </li>
      </ul>
    </div>
    <div class="title">
      <p>{{ title }}</p>
    </div>
    <hr>
    <div class="page1" v-if="showPage == 'page1'">
      <div class="mb-3">
        <label class="form-label">服務類別<span class="star">*</span></label>
        <select class="form-control form-select" v-model="serviceData.serviceRequirementId"
          v-if="jobTypeList.length > 0">
          <option v-for="item in jobTypeList" :value=item.id :key="item.id">{{ item.name }}</option>
        </select>
      </div>
      <div class="mb-3">
        <label class="form-label">服務人員性別<span class="star">*</span></label>
        <div class="input-group">
          <div class="col form-check">
            <input type="radio" class="form-check-input" value="None" id="None" v-model="serviceData.gender">
            <label class="form-check-label" for="None">不指定</label>
          </div>
          <div class="col form-check">
            <input type="radio" class="form-check-input" value="Male" id="Male" v-model="serviceData.gender">
            <label class="form-check-label" for="Male">男性</label>
          </div>
          <div class="col form-check">
            <input type="radio" class="form-check-input" value="Female" id="Female" v-model="serviceData.gender">
            <label class="form-check-label" for="Female">女性</label>
          </div>
        </div>
      </div>
      <div class="mb-3">
        <label class="form-label">服務年資<span class="star">*</span></label>
        <div class="input-list" v-if="expList.length > 0">
          <div class="form-check-inline" v-for="item in expList" :key="item.id">
            <input type="radio" class="form-check-input" :value="'exp' + item.id" :id="'exp' + item.id"
              v-model="serviceData.seniorityId">
            <label class="form-check-label" :for="'exp' + item.id">{{ item.name }}</label>
          </div>
        </div>
      </div>
      <div class="mb-3">
        <label class="form-label">語言能力(複選)<span class="star">*</span></label>
        <div class="input-list" v-if="langList.length > 0">
          <div class="form-check-inline" v-for="item in langList" :key="item.id">
            <input type="checkbox" class="form-check-input" :value="'lang' + item.id" :id="'lang' + item.id"
              v-model="serviceData.languageId">
            <label class="form-check-label" for="'lang'+item.id">{{ item.name }}</label>
          </div>
        </div>
      </div>
      <div class="mb-3">
        <label class="form-label">是否需具備該服務類別專業證照<span class="star">*</span></label>
        <div class="input-group">
          <div class="col form-check">
            <input type="radio" class="form-check-input" value="1" id="yes" v-model="serviceData.isHasLicense">
            <label class="form-check-label" for="yes">是</label>
          </div>
          <div class="col form-check">
            <input type="radio" class="form-check-input" value="0" id="no" v-model="serviceData.isHasLicense">
            <label class="form-check-label" for="no">否</label>
          </div>
        </div>
      </div>

    </div>
    <div class="page2" v-if="showPage == 'page2'">
      <div class="mb-3">
        <label class="form-label">服務需求(複選)<span class="star"></span></label>
        <div class="input-pill">
          <div class="row" v-if="serviceRequirementList.length > 0" required>
            <div class="col-4 form-check" v-for="item in serviceRequirementList" :key="item.id">
              <input type="checkbox" class="form-check-input" :value="item.id" :id="item.id"
                v-model="serviceData.serviceId">
              <label class="form-check-label" :for="item.id">{{ item.name }}</label>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

  <footer class="c-footer">
    <div class="ui-tabbtn">
      <div class="row">
        <div class="col">
          <button class="btn btn-primary" type="button" @click="save" v-if="showPage == 'page1'">確認並繼續</button>
          <button class="btn btn-primary" type="button" @click="save2" v-if="showPage == 'page2'">完成新增</button>
        </div>
      </div>
    </div>
  </footer>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

Header {
  background-color: #FFFFFF;
}

.requirementAdd {
  margin-bottom: calc($footer-height + 15px);
  margin-top: $header-height;
  padding: 1.25rem;
}

.title {
  font-size: 1.25rem;
  font-weight: 700;
  display: flex;
  justify-content: center;
}
</style>